	module.exports = {"data":{"post":{"html":"<p>In their reporting on the 40th anniversary of the <a title=\"Economist.com\" href=\"http://www.economist.com/agenda/displayStory.cfm?story_id=1545760\">Elysée treaty</a> (and before the Rumsfeld remarks on 'Old Europe'), our <a href=\"http://www.ndrinfo.de\">local news radio station</a> interviewed people in Paris about their stereotype of Germans. The best answer they saved until the end.</p>\n<p>\"The typical German is just like the typical Frenchman. They always know better and never listen.\"</p>","fields":{"title":"Old European Stereotypes","date":"26 Jan 2003","url":"https://papascott.de/2003-01-26-old-european-stereotypes/"}}},"pathContext":{"slug":"/2003-01-26-old-european-stereotypes/","prev":{"fields":{"title":"Axis of Small Mammals","date":"24 Jan 2003","slug":"/2003-01-24-axis-of-small-mammals/"}},"next":{"fields":{"title":"An Axe for the Toolbox","date":"28 Jan 2003","slug":"/2003-01-28-an-axe-for-the-toolbox/"}}}}

